import React, { useEffect, useState } from "react"
import CustomLink from "./CustomLink"

import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons"

const styles = {
  navbar: {
    backgroundColor: "#7D3C98",
  },
  center: { display: "flex", justifyContent: "center", alignItems: "center" },
}

const collapsedHeight = 55
const expandedHeight = 200

export default function NavMenu(props) {
  const [atBottom, setAtBottom] = useState(false)
  const [isExpanded, setIsExpanded] = useState(false)
  const atBottomProp = props.atBottom
  const [navbarHeight, setNavbarHeight] = useState(collapsedHeight)
  useEffect(() => {
    setAtBottom(atBottomProp)
  }, [atBottomProp])
  const handleOnClick = () => {
    setIsExpanded(!isExpanded)
  }
  useEffect(() => {
    const newNavbarHeight = isExpanded ? expandedHeight : collapsedHeight
    setNavbarHeight(newNavbarHeight)
  }, [isExpanded])

  return (
    <Navbar
      style={{ ...styles.navbar }}
      variant="dark"
      expand="sm"
      sticky="top"
    >
      <Navbar.Brand href="/">
        <strong style={{ fontWeight: 700 }}>Winton Nathan-Roberts</strong>
      </Navbar.Brand>
      <Navbar.Toggle
        aria-controls="basic-navbar-nav"
        onClick={handleOnClick}
        style={{ border: "0" }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {isExpanded && <FontAwesomeIcon icon={faTimes} size="2x" />}
          {!isExpanded && <FontAwesomeIcon icon={faBars} size="2x" />}
        </div>
      </Navbar.Toggle>
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="ml-auto" variant="pills">
          <CustomLink
            activeClass={atBottom ? "x" : "active"}
            to="aboutme"
            spy={true}
            smooth={true}
            offset={-navbarHeight-20}
            duration={500}
          >
            <strong style={{ fontWeight: 700, color: "white" }}>ABOUT</strong>
          </CustomLink>
          <CustomLink
            activeClass={atBottom ? "x" : "active"}
            to="portfolio"
            spy={true}
            smooth={true}
            offset={-navbarHeight}
            duration={500}
          >
            <strong style={{ fontWeight: 700, color: "white" }}>
              PORTFOLIO
            </strong>
          </CustomLink>
          <CustomLink
            activeClass="active"
            to="contact"
            spy={true}
            smooth={true}
            offset={-navbarHeight}
            active={atBottom}
            duration={500}
          >
            <strong style={{ fontWeight: 700, color: "white" }}>CONTACT</strong>
          </CustomLink>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}
